import { _decorator, Component, instantiate, Node, Prefab } from 'cc';
import { ViewPop } from '../../../framework/base/ViewPop';
import { tab } from '../../../Table/table_gen';
import { HeroDataControl } from '../hero/herobag/HeroDataControl';
const { ccclass, property } = _decorator;

@ccclass('PictureScrollView')
export class PictureScrollView extends ViewPop {
    @property(Node)
    node_content: Node = null;
    @property(Prefab)
    pfb_item: Prefab = null;
    register(): void {

    }
    onShow(): void {
        // 上来默认是猎手
        let list = HeroDataControl.ins.getPaintingListByVocation(tab.HeroClass.HeroClass_Archer);
        for (let i = 0; i < list.length; i++) {
            let heroId = list[i];
            let item = this.node_content.children[i];
            if (!item) {
                item = instantiate(this.pfb_item);
                item.parent = this.node_content
            }
        }
    }
}


